body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

:root {
  --navbar-height: 40px;
  scroll-behavior: smooth;
  overflow-x: hidden;
}

.App {
  text-align: center;
  background-color: black;
  height: auto;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  min-width: 100%;
}

.home-div, .hobbies-div, .contact-div {
  padding-top: 40px;
  padding-top: var(--navbar-height);
  min-height: 90vh;
  min-width: 100%;
}

.interval-div {
  background-color: rgb(16, 14, 19);
  height: 15vh;
}

.loader-div {
  color: white;
  height: 100vh;
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}

.loader-div h1{
  color: white;
  -webkit-animation: shines 1.5s linear infinite;
  animation: shines 1.5s linear infinite;
}

@-webkit-keyframes shines {
  0% {opacity: 100%;}
  50% {opacity: 30%;}
  100% {opacity: 100%;}
}

@keyframes shines {
  0% {opacity: 100%;}
  50% {opacity: 30%;}
  100% {opacity: 100%;}
}
.navbar-div {
  position: fixed;
  top: 0;
  right: 0;
  height: var(--navbar-height);
  width: 100%;
  display: flex;
  justify-content: center;
  background-color: red;
  font-size: larger;
  z-index: 2;
}
.navbar-div a{
  margin: 5px 20px 0 20px;
}

.clock-div {
  margin: 5px 10px 0 200px;
}
.home-div {
  background-color: rgb(182, 182, 0);
  background-image: url(/static/media/pattern.b6c0d73d.png);
  margin-top: var(--navbar-height);
  display: flex;
  flex-direction: column;
}

.home-div h1 {
  padding-left: 10px;
  padding-right: 10px;
}

.header-div {
  height: -webkit-fit-content;
  height: -moz-fit-content;
  height: fit-content;
  border-left: 5px solid rgb(16, 0, 43);
  overflow-x: hidden;
  margin: 0 0 10px 50px;
  white-space: nowrap;
  max-width: -webkit-fit-content;
  max-width: -moz-fit-content;
  max-width: fit-content;
}

.header-div:hover {
  transition: background-color 1s ease-in-out;
  background-color: rgb(187, 223, 190);
  transition-timing-function: cubic-bezier(0.1, 0.7);
}

.typing{
  -webkit-animation: typing-rect 0.6s infinite;
          animation: typing-rect 0.6s infinite;
  -webkit-animation-delay: 1.6s;
          animation-delay: 1.6s;
}

@-webkit-keyframes typing-rect {
  0% {border-right: 3px solid white;}
  50% {border-right: 3px solid white;}
  510% {border-right: 3px solid transparent;}
  100% {border-right: 3px solid transparent;}
}

@keyframes typing-rect {
  0% {border-right: 3px solid white;}
  50% {border-right: 3px solid white;}
  510% {border-right: 3px solid transparent;}
  100% {border-right: 3px solid transparent;}
}

.cascade-wrapper {
  display: flex;
  flex-direction: row;
  justify-content: center;
}

.left-cascade-div, .right-cascade-div{
  border: 2px solid rgb(124, 0, 0);
  padding: 10px;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  margin-left: 10px;
  box-shadow: 2px 2px 5px rgb(58, 0, 0);
  background-color: rgb(255, 230, 0);
}

.right-cascade-div {
  background-color: rgb(0, 41, 25);
  color: aquamarine;
}

.right-cascade-div .header-div:hover {
  transition: background-color 1s ease-in-out;
  background-color: rgb(63, 117, 0);
  transition-timing-function: cubic-bezier(0.1, 0.7);
}

.float-div {
  position: absolute;
  height: 30px;
  width: 70px;
  background-color: aliceblue;
  right: 100px;
  z-index: 1;
  border-radius: 50% ;
  box-shadow: 0 5px 10px gray;
  -webkit-animation: float 3s ease-in-out infinite;
          animation: float 3s ease-in-out infinite;
  display: inline-block;
}

.float-div a {
  text-decoration: none;
}

.float-div span{
  color: green;
  font-size: 1.2rem;
  margin-left: 15px;
}

@-webkit-keyframes float {
  0% {-webkit-transform: translateY(0);transform: translateY(0);}
  50% {-webkit-transform: translateY(10px);transform: translateY(10px);}
  100% {-webkit-transform: translateY(0);transform: translateY(0);}
}

@keyframes float {
  0% {-webkit-transform: translateY(0);transform: translateY(0);}
  50% {-webkit-transform: translateY(10px);transform: translateY(10px);}
  100% {-webkit-transform: translateY(0);transform: translateY(0);}
}

.drawer-container {
  margin-top: 4rem;
  display: flex;
  flex-direction: row;
  margin: auto;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  height: 50rem;
}

.drawer-div {
  margin: 4rem;
  height: 20rem;
  width: 10rem;
  background-color: rgb(138, 5, 5);
  margin-bottom: 4rem;
  border: 8px inset rgb(122, 3, 3);
}

.hobbies-div {
  background-color: aqua;
  background-image: url(/static/media/potato.895a91dc.png);
  background-position: center;
  background-size: cover;
}

.hobbies-div h1, h2 {
  text-shadow: 2px 1px 0px white;
}

.hobbies-div a {
  text-decoration: none;
  color: rgb(56, 102, 55);
}

.language-wrapper {
  display: flex;
  justify-content: center;
}

.language-wrapper li {
  list-style-type: none;
  float: left;
  padding: 0 10px 2px 10px;
  margin: 4px 4px 10px 14px;
  background-color: rgb(110, 189, 253);
  border-bottom: 2px solid darkblue;
  border-radius: 5px;
}

.language-wrapper h3 {
  padding: 0 8px 2px 8px;
  margin: 4px 4px 10px 4px;
  background-color: rgb(110, 253, 193);
  border-bottom: 2px solid darkgreen;
  border-radius: 5px;
}

.language-div, .chess-div, .procreate-div, .gym-div, .tarot-div, .youtube-div {
  min-height: 80vh;
}

.language-item {
  border-radius: 20px;
  margin: 5px 20px 5px 20px;
  background-color: bisque;
  height: -webkit-fit-content;
  height: -moz-fit-content;
  height: fit-content;
  width: 300px;
  border-bottom: 2px solid darkblue;
  box-shadow: 2px 2px 1px gray;
}

.youtube-div {
  padding-top: var(--navbar-height);
}

.youtube-link {
  background-color: red;
  width: 320px;
  height: 80px;
  border-radius: 20px;
  margin: auto;
  border: 5px solid transparent;
  box-shadow: 3px 3px 10px gray;
  padding-bottom: 5px;
}

.youtube-link:hover {
  border: 5px solid rgb(175, 0, 0);
  background-color: rgb(252, 77, 77);
}

.youtube-frame {
  box-shadow: 4px 4px 5px black;
}

.youtube-link:active {
  background-color: rgb(219, 0, 0);
}

.chess-div a:link {
  display: inline-block;
}

.chess-profile {
  width: 320px;
  height: 80px;
  background-image: url("https://upload.wikimedia.org/wikipedia/commons/d/d7/Chessboard480.svg");
  border: 5px solid rgb(115, 117, 0);
  box-shadow: 4px 4px 0 rgb(73, 75, 0);
  background-size: contain;
  margin: auto;
  text-shadow: 3px 3px 3px rgb(217, 185, 253);
}

.chess-profile:hover {
  background-image: url("https://upload.wikimedia.org/wikipedia/commons/d/d5/Chess_Board.svg");
  border: 5px solid rgb(255, 255, 255);
  box-shadow: 4px 4px 0 rgb(130, 134, 134);
}

.opening-div {
  font-family: 'Times New Roman', Times, serif;
  font-size: larger;
  list-style-type: none;
  width: 20%;
  margin: auto;
  background-image: url("https://upload.wikimedia.org/wikipedia/commons/d/d7/Chessboard480.svg");
  background-size: contain;
  text-shadow: 2px 2px 0px gray;
  border: 5px solid rgb(95, 89, 0);
  box-shadow: 4px 4px 0 rgb(56, 52, 0);
}

.gym-squat {
  position: absolute;
  margin: auto;
  right: 0;
  padding: 0 30px 0 0;
  height: -webkit-fit-content;
  height: -moz-fit-content;
  height: fit-content;
  width: 100px;
  background-color: transparent;
}

.squat-img {
  height: 100px;
  width: 100px;
  background-image: url(data:image/png;base64,);
}

.squat-img:active {
  -webkit-animation: squat 0.5s linear;
          animation: squat 0.5s linear;
}

@-webkit-keyframes squat {
  0% {background-image: url(data:image/png;base64,)}
  25% {background-image: url(data:image/png;base64,)}
  50% {background-image: url(data:image/png;base64,)}
  75% {background-image: url(data:image/png;base64,)}
  100% {background-image: url(data:image/png;base64,)}
}

@keyframes squat {
  0% {background-image: url(data:image/png;base64,)}
  25% {background-image: url(data:image/png;base64,)}
  50% {background-image: url(data:image/png;base64,)}
  75% {background-image: url(data:image/png;base64,)}
  100% {background-image: url(data:image/png;base64,)}
}
.contact-div {
  min-height: 90vh;
  width: 100vw;
  background: rgb(70,117,67);
  background: linear-gradient(0deg, rgba(70,117,67,1) 0%, rgba(48,255,106,1) 100%, rgba(0,212,255,1) 100%);
}

.freelance-header {
  background-color: aquamarine;
  width: 50%;
  margin: auto;
  box-shadow: 2px 2px 5px black;
}
